$primary-color : 	oklch(40.233% 0.14406 26.637);

*{
  -ms-overflow-style: none;
  &::selection{
    color: white;
    background-color: $primary-color;
  }
 
  &::-webkit-scrollbar {
    display: none;
  }
}

iframe {
  border: none;
  overflow: hidden;
}

