  margin: 0;
  padding: 0;
}

.bun{
  font-size: 16px;
}

.highlight {
  position: relative;
  font-family: 'Noto Sans JP', sans-serif;

  //color:$white;
  //font-weight: bold;
  z-index: 0;

  rt{
    //color: $black;
    position: relative;
    z-index: 0;
    user-select: none;
  }
  &::before {
    content: '';
    position: absolute;
    height: 80%;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: $primary-color;
  }
  //box-shadow: inset 0 -16px 0 hsla(0, 90%, 83%, 0.93);
}

.bold {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 700;
}
